@media print {
  img,
  header,
  footer,
  form,
  figure,
  aside,
  iframe,
  .sidepanel,
  .back-to-top,
  .wpcf7,
  .fa,
  .cm.sh {
    display: none !important;
  }
  /* This Removes the URL of anchor tag but retain the text */
  a {
    pointer-events: none !important;
  }
  a[href]:after {
    content: none !important;
  }
  .wrapper-holder {
    min-height: 10px !important;
    margin-top: 0 !important;
    padding-bottom: 0 !important;
  }
  div {
    height: auto !important;
    padding: 0 !important;
    margin: 0 !important;
    min-height: 10px !important;
    width: 100% !important;
  }
  * {
    opacity: 1 !important;
    position: initial !important;
    text-align: initial !important;
    margin: 0 !important;
    padding: 0 !important;
  }
  h1, h2, h3, h4, h5, h6 {
    margin-top: 10px !important;
    margin-bottom: 15px !important;
  }
  p + p {
    margin-top: 10px !important;
  }
  p ~ table {
    margin-top: 10px !important;
    margin-bottom: 10px !important;
  }
  table {
    border-top: 1px solid #ccc !important;
    border-right: 1px solid #ccc !important;
  }
  table td, table th {
    padding: 5px 10px !important;
    border-left: 1px solid #ccc !important;
    border-bottom: 1px solid #ccc !important;
  }
  table ol, table ul {
    padding-left: 15px !important;
  }
}

@media screen {
  main, .wrapper-holder {
    position: relative;
    z-index: 1;
  }
  .form-control::-webkit-input-placeholder {
    color: #b5b5b5;
  }
  .form-control:-moz-placeholder {
    color: #b5b5b5;
  }
  /* Firefox 18- */
  .form-control::-moz-placeholder {
    color: #b5b5b5;
  }
  /* Firefox 19+ */
  .form-control:-ms-input-placeholder {
    color: #b5b5b5;
  }
  .form-control {
    border-radius: 0;
    height: 40px;
    padding: 10px 20px;
  }
  span.wpcf7-not-valid-tip {
    display: none !important;
  }
  div.wpcf7-validation-errors {
    border-color: #e46967;
    background-color: #FFCFCF;
    color: #e46967;
    margin: 0 0 10px;
  }
  .wpcf7-not-valid {
    border: 1px solid #e46967;
    background-color: #FFCFCF;
  }
  .wpcf7-mail-sent-ok {
    border: 2px solid #3c763d;
    margin: 0 0 10px;
    color: #3c763d;
    background-color: #DFF0D8;
  }
  img.alignleft {
    float: left;
    margin-right: 20px;
  }
  img.aligncenter {
    display: block;
    margin: 0 auto 20px;
  }
  img.alignright {
    float: right;
    margin-left: 20px;
  }
  .breadcrumbs {
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    font-size: 14px;
    padding: 5px 15px;
    margin-bottom: 0 !important;
  }
  .breadcrumbs .fa {
    padding-left: 5px;
    padding-right: 5px;
  }
  .img-thumbnail {
    padding: 5px;
    border-radius: 0;
    border: none;
    box-shadow: 0 0 2px 1px #ccc;
  }
  .acf-map {
    width: 100%;
    height: 400px;
    border: #ccc solid 1px;
    margin: 20px 0;
  }
  /* fixes potential theme css conflict */
  .acf-map img {
    max-width: inherit !important;
  }
  .loader-overlay {
    background: rgba(255, 255, 255, 0.98);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 999;
  }
  .loader-overlay .loader {
    bottom: 0;
    left: 30px;
    position: absolute;
    right: 30px;
    top: 0;
  }
  .loader-overlay .loader .loader-img {
    bottom: 0;
    color: rgba(0, 0, 0, 0.7);
    display: block;
    height: 100%;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: 0;
    width: 100%;
  }
  .loader-overlay .loader .loader-img span {
    display: block;
    vertical-align: middle;
  }
  .back-to-top {
    position: fixed;
    bottom: 15px;
    right: 15px;
    z-index: 9;
  }
  .back-to-top a {
    background-color: #ccc;
    color: #000;
    padding: 12px 15px;
    border-radius: 5px;
  }
  .cm {
    display: inline-block;
    float: none;
    vertical-align: middle;
    width: 99.99%;
  }
  .cm.sh {
    width: 0.01%;
    height: 100%;
  }
  .carousel-fade .carousel-inner .item {
    transition-property: opacity;
  }
  .carousel-fade .carousel-inner .item,
  .carousel-fade .carousel-inner .active.left,
  .carousel-fade .carousel-inner .active.right {
    opacity: 0;
  }
  .carousel-fade .carousel-inner .active,
  .carousel-fade .carousel-inner .next.left,
  .carousel-fade .carousel-inner .prev.right {
    opacity: 1;
  }
  .carousel-fade .carousel-inner .next,
  .carousel-fade .carousel-inner .prev,
  .carousel-fade .carousel-inner .active.left,
  .carousel-fade .carousel-inner .active.right {
    left: 0;
    transform: translate3d(0, 0, 0);
  }
  .carousel-fade .carousel-control {
    z-index: 2;
  }
  .column-middle {
    display: inline-block;
    float: none;
    vertical-align: middle;
    margin: 0 -2px;
  }
  .column-top {
    display: inline-block;
    float: none;
    vertical-align: top;
    margin: 0 -2px;
  }
  .column-bottom {
    display: inline-block;
    float: none;
    vertical-align: bottom;
    margin: 0 -2px;
  }
  .g-recaptcha {
    margin-top: 0px;
    height: auto;
  }
}

@media screen and (max-width: 400px) {
  .g-recaptcha {
    height: 60px;
    overflow: hidden;
  }
}

@media screen {
  .g-recaptcha > div {
    margin: 0;
  }
}

@media screen and (max-width: 400px) {
  .g-recaptcha > div {
    transform: scale(0.77);
    -webkit-transform: scale(0.77);
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
    margin: 0 auto;
  }
}

@media screen {
  span.wpcf7-form-control-wrap .g-recaptcha {
    margin-top: -15px;
    margin-bottom: 15px;
  }
}
